.feature-placeholder-image {
  background: linear-gradient(135deg, rgba(13, 123, 62, 0.2), rgba(16, 176, 104, 0.1));
  height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.7);
