  .features-grid {
    grid-template-columns: 1fr;
  }
  
  .feature-card {
    padding: 20px;
  }
  
  .feature-card-icon {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .feature-placeholder-image {
    height: 350px;
  }
}

